import React from 'react'

import './App.css'

var sample = require('lodash/sample');

var beginning = ["wallah", "wesh", "mamene", "des barres", "nan mais what", "heuu", "alors"]
var followup = ["y a", "c'est", "l'autre fois y a", "il m'est encore arrivé un truc, y a"]
var subjects = ["jean-michel", "jean-claude", "jean-cancer", "un renoich", "jean-coufard", "un keuk", "un chinois", "un paki", "un indien", "un chibron"]
var adjectives = ["pas tres chillax", "obscur", "éclaté", "claqué au sol", "claqué du cul", "allumé", "ravagé", "zéro chill", "bourré", "énervé", "vénere", "flou", "du chibre", "chibré"]
var verbs = ["marabouté", "retourné le cerveau", "matrixé", "coufardisé", "halpagué", "attrapé", "sorti des chinoiseries", "balancé des baux obscurs", "victimisé", "confus", "sorti une phrase obscure", "parlé d'un bail sombre", "montré sa poutre de bamako"]
var adverbs = ["de ouf", "en soum soum", "mon frere", "en mode 'eh t'es un ouf'", "en mode 'steuplait'"]
var ends = ["je me suis envolé",
        "c'est parti en octogone sans regle",
        "jamais je lui reparle",
        "il me fait peur",
        "je suis dans le doute",
        "ca m'a confus",
        "je me suis laissé faire",
        "je l'ai defoncé",
        "c'etait haram",
        "c'etait terrible",
        "j'etais confus",
        "il m'a retourné le cerveau"]

class App extends React.Component {
    state = { sentence: '' }

    static buildSentence(){
        return sample(beginning) + ", " + sample(followup) + " " + sample(subjects) + " " + sample(adjectives) + " qui m'a " + sample(verbs) + " " + sample(adverbs) + ", " + sample(ends) + "."
    }

    componentDidMount(){
        this.getSentence();
    }

    getSentence(){
        var sentence = App.buildSentence();
        this.setState({ sentence: sentence });
    }

    render() {
        const { sentence } = this.state;
        return (
            <div>
            <div className="app">
            <h1>ALBERTRON</h1>
            <div className="card">
              <h2>{sentence}</h2>
              <button className="buton" onClick={e => this.getSentence()}>
                <span><h4>More of Albert</h4></span>
              </button>
            </div>
          </div>
          <footer>
              <div>
                All rights reserved.
              </div>
          </footer>
          </div>

        );
    }
}

export default App